<script>
import $ from "_jquery@3.6.0@jquery"
export default {
  name: 'mainPage',
  props: {
    msg: String
  },
  data () {
    return {
      list:[]
    }
  },
  methods: {
    getList () {
      const that = this
      $.getJSON("./lunyu.json",function (data) {
        that.list = data
      })
    }
  },
  mounted () {
    this.getList()
  }
}
</script>

